import React from 'react';

interface SocialIconProps {
  url: string;
  icon: string;
}

const SocialIcon: React.FC<SocialIconProps> = ({ url, icon }) => {
  return (
    <a 
      href={url} 
      target="_blank" 
      rel="noopener noreferrer"
      className="inline-block m-1 hover:scale-110 transition-transform duration-200"
    >
      <div className="w-8 h-8 p-1">
        <img 
          src={`/socials/${icon}`}
          alt={`Social media icon for ${icon.replace('.svg', '')}`}
          className="w-full h-full filter hue-rotate-60 hover:hue-rotate-0 transition-all duration-200"
        />
      </div>
    </a>
  );
};

export default SocialIcon;