export type Vec2 = [number, number];
export type Mat2 = [Vec2, Vec2];
export type Vec4 = [number, number, number, number];

const rotFromAngle = (angle: number): Mat2 => {
  const c = Math.cos(angle);
  const s = Math.sin(angle);
  return [[c, -s] as Vec2, [s, c] as Vec2];
};

const transpose = (M: Mat2): Mat2 => {
  return [[M[0][0], M[1][0]] as Vec2, [M[0][1], M[1][1]] as Vec2];
};

const mag2 = (e: Vec2): number => {
  return e.reduce((acc, v) => acc + v * v, 0);
};

const mag = (e: Vec2): number => {
  return Math.sqrt(mag2(e));
};

const minus = (vA: Vec2, vB: Vec2): Vec2 => {
  const out: Vec2 = [0, 0] as Vec2;
  out[0] = vA[0] - vB[0];
  out[1] = vA[1] - vB[1];
  return out;
};

const dist = (v0: Vec2, v1: Vec2): number => {
  return mag(minus(v0, v1));
};

const normalize = (e: Vec2): Vec2 => {
  const m = mag(e);
  return e.map((v) => v / m) as Vec2;
};

/**
 * Multiplies a matrix by a vector.
 *
 * @param M - The matrix to be multiplied.
 * @param pos - The vector to multiply the matrix by.
 * @returns The resulting vector.
 */
const matVecMult = (M: Mat2, pos: Vec2): Vec2 => {
  return M.map((row) => row.reduce((acc, v, i) => acc + v * pos[i], 0)) as Vec2;
};

const matMatMult = (M0: Mat2, M1: Mat2): Mat2 => {
  return M0.map(
    (row) =>
      row.map((_, j) =>
        row.reduce((acc, v, k) => acc + v * M1[k][j], 0)
      ) as Vec2
  ) as Mat2;
};
/*
const invertMat = (M: Mat2): Mat2 => {
  const det = M[0][0] * M[1][1] - M[0][1] * M[1][0];
  const epsilon = 1e-10;
  if (Math.abs(det) < epsilon) {
    throw new Error("Matrix is singular and cannot be inverted.");
  }
  const invDet = 1 / det;
  return [
    [ M[1][1] * invDet, -M[0][1] * invDet ],
    [ -M[1][0] * invDet, M[0][0] * invDet ]
  ] as Mat2;
};
*/
const solve = (M: Mat2, b: Vec2): Vec2 => {
  //robust solver for 2x2 matrix
  const det = M[0][0] * M[1][1] - M[0][1] * M[1][0];
  const epsilon = 1e-10;
  if (Math.abs(det) < epsilon) {
    throw new Error("Matrix is singular and cannot be inverted.");
  }
  const invDet = 1 / det;
  const x = (M[1][1] * b[0] - M[0][1] * b[1]) * invDet;
  const y = (-M[1][0] * b[0] + M[0][0] * b[1]) * invDet;
  return [x, y] as Vec2;
};
/**
 * Multiplies a vector by a scalar.
 *
 * @param s - The scalar.
 * @param v - The vector.
 * @returns The resulting vector.
 */
const sMult = (s: number, v: Vec2): Vec2 => {
  return v.map((e) => e * s) as Vec2;
};

const avg = (vA: Vec2, vB: Vec2): Vec2 => {
  return vA.map((e, i) => (e + vB[i]) / 2) as Vec2;
};

const perp = (v: Vec2): Vec2 => [-v[1], v[0]];

const dot = (va: Vec2, vb: Vec2): number => {
  return va.reduce((acc, v, i) => acc + v * vb[i], 0);
};

const lerp = (t: number, p0: Vec2, p1: Vec2): Vec2 => {
  return p0.map((v, i) => v + t * (p1[i] - v)) as Vec2;
};

const arrayDiffNorm = (vA: Vec2, vB: Vec2): number => {
  return Math.sqrt(
    vA.reduce((acc, v, i) => acc + (v - vB[i]) * (v - vB[i]), 0)
  );
};

const add = (vA: Vec2, vB: Vec2): Vec2 => {
  return vA.map((e, i) => e + vB[i]) as Vec2;
};

const atan2 = (v: Vec2): number => {
  return Math.atan2(v[1], v[0]);
};

const calcAngle = (vA: Vec2, vB: Vec2): number => {
  const dv = normalize(minus(vA, vB));
  return Math.atan2(dv[1], dv[0]) - Math.PI;
};

const rotFromTwoVectors = (vA: Vec2, vB: Vec2): Mat2 => {
  return rotFromAngle(calcAngle(vA, vB));
};

const rotateVector = (v: Vec2, angle: number): Vec2 => {
  const c = Math.cos(angle);
  const s = Math.sin(angle);
  return [c * v[0] - s * v[1], s * v[0] + c * v[1]];
};

const clamp = (v: number, min: number, max: number): number =>
  Math.min(Math.max(v, min), max);

const signedAngle = (v1: Vec2, v2: Vec2): number => {
  const [x1, y1] = v1;
  const [x2, y2] = v2;
  const dt = dot(v1, v2);
  const mag1 = mag(v1);
  const mag2 = mag(v2);
  if(mag1 === 0 || mag2 === 0) return 0;
  const cosTheta = clamp(dt / (mag1 * mag2), -1.0, 1.0);
  const angle = Math.acos(cosTheta);
  const crossProd = x1 * y2 - y1 * x2;
  const sign = crossProd >= 0 ? 1 : -1;
  if (Number.isNaN(angle)) {
    console.log(dot, mag1, mag2, cosTheta, angle, crossProd, sign);
  }
  return sign * angle;
};

const closestPointToInfiniteLine = (p: Vec2, p0: Vec2, p1: Vec2): Vec2 => {
  const v = minus(p1, p0);
  const w = minus(p, p0);
  const c1 = dot(w, v);
  const c2 = dot(v, v);
  const b = c1 / c2;
  return add(p0, sMult(b, v));
};

const closestPointToCircle = (p: Vec2, pcen: Vec2, r: number): Vec2 => {
  const v = minus(p, pcen);
  const d = mag(v);
  return add(pcen, sMult(r / d, v));
};

const closestPoint = (p: Vec2, p0: Vec2, p1: Vec2): Vec2 => {
  const v = minus(p1, p0);
  const w = minus(p, p0);
  const c1 = dot(w, v);
  if (c1 <= 0) return p0;
  const c2 = dot(v, v);
  if (c2 <= c1) return p1;
  const b = c1 / c2;
  return add(p0, sMult(b, v));
};

const project = (a: Vec2, b: Vec2): Vec2 => {
  const adb = dot(a, b);
  const bdb = dot(b, b);
  return sMult(adb / bdb, b);
};

const reject = (a: Vec2, b: Vec2): Vec2 => {
  const apb = project(a, b);
  return minus(a, apb);
};

/**
 * Reflects a vector off a plane defined by a normal.
 */
const reflect = (N: Vec2, v: Vec2): Vec2 => {
  const ndn = dot(N, N);
  const vdn = dot(v, N);
  const vpn = sMult((2 * vdn) / ndn, N);
  return minus(vpn, v);
};

const circleReflect = (p: Vec2, pcen: Vec2, r: number): Vec2 => {
  const v = minus(p, pcen);
  const d = mag(v);
  const n = sMult(1 / d, v);
  const p0 = add(pcen, sMult(r, n));
  return add(p0, sMult(r - d, n));
};

const cross = (vA: Vec2, vB: Vec2): number => {
  return vA[0] * vB[1] - vA[1] * vB[0];
};

const getArcAngles = (
  pcen: Vec2,
  p1: Vec2,
  p2: Vec2
): [number, number, number, number] => {
  let dp0 = minus(p1, pcen);
  let dp1 = minus(p2, pcen);
  const r0 = mag(dp0);
  const r1 = mag(dp1);
  dp0 = normalize(dp0);
  dp1 = normalize(dp1);
  const dir = Math.sign(r0 - r1 + 1e-10);
  let angle0 = Math.atan2(dp0[1], dp0[0]);
  let angle1 = Math.acos(dot(dp0, dp1));
  angle0 = angle0 < 0 ? angle0 + 2.0 * Math.PI : angle0;
  const crossProd = dp0[0] * dp1[1] - dp0[1] * dp1[0];
  if (crossProd < 0.0 && dir > 0) {
    angle1 = 2.0 * Math.PI - angle1;
  } else if (crossProd > 0.0 && dir < 0) {
    angle1 = 2.0 * Math.PI - angle1;
  }
  return [r0, r1, angle0, dir * angle1];
};

const genSpiral = (
  cen: Vec2,
  r0: number,
  r1: number,
  theta0: number,
  dtheta: number,
  N: number
): Vec2[] => {
  const points = new Array(N).fill([0, 0] as Vec2);
  return points.map((_, i) => {
    const t = i / (N - 1);
    const r = r0 + t * (r1 - r0);
    const a = theta0 + t * dtheta;
    const dp = polarToCartesian(r, a);
    return add(cen, dp);
  });
};

const genBezierSpiral = (
  cen: Vec2,
  rs: [number, number, number, number],
  ts: [number, number, number, number],
  N: number
): Vec2[] => {
  const points = new Array(N).fill([0, 0] as Vec2);
  return points.map((_, i) => {
    const t = i / (N - 1);
    const dp = getBezierSpiralPoint(rs, ts, t);
    return add(cen, dp);
  });
};

const genBezierSpiralTan = (
  cen: Vec2,
  rs: [number, number, number, number],
  ts: [number, number, number, number],
  N: number
): Vec2[] => {
  const points = new Array(N).fill([0, 0] as Vec2);
  return points.map((_, i) => {
    const t = i / (N - 1);
    const dp = getBezierSpiralTan(rs, ts, t);
    return dp;
  });
};

const getCirclePoint = (pcen: Vec2, r: number, t0: number): Vec2 => {
  const parX = r * Math.cos(t0);
  const parY = r * Math.sin(t0);
  return add(pcen, [parX, parY]);
};

const getSpiralTan = (
  r0: number,
  r1: number,
  t0: number,
  t1: number,
  s: number
): Vec2 => {
  const t = t0 + s * (t1 - t0);
  const r = r0 + s * (r1 - r0);
  const rp = r1 - r0;
  const tp = t1 - t0;
  const tx = rp * Math.cos(t) - r * tp * Math.sin(t);
  const ty = rp * Math.sin(t) + r * tp * Math.cos(t);
  return normalize([tx, ty]);
};

const cubic = (v: [number, number, number, number], t: number): number => {
  const t1 = 1.0 - t;
  return (
    t1 * t1 * t1 * v[0] +
    3.0 * t1 * t1 * t * v[1] +
    3.0 * t1 * t * t * v[2] +
    t * t * t * v[3]
  );
};

const dcubic = (v: [number, number, number, number], t: number): number => {
  const t1 = 1.0 - t;
  return (
    3.0 *
    (t1 * t1 * (v[1] - v[0]) +
      2.0 * t * t1 * (v[2] - v[1]) +
      t * t * (v[3] - v[2]))
  );
};


const calcBezierControlFromTangent = (q: Vec2, tan: Vec2) => {
  const [r, o] = q;
  const A = Math.cos(o);
  const B = Math.sin(o);
  const dx = tan[0] / 3.0;
  const dy = tan[1] / 3.0;
  const M = [
    [A, -r * B],
    [B, r * A],
  ] as Mat2;
  const b = [dx + r * A - r * o * B, dy + r * B + r * o * A] as Vec2;
  return solve(M, b);
};

const getBezierSpiralPoint = (
  rs: [number, number, number, number],
  ts: [number, number, number, number],
  t: number
): Vec2 => {
  const r = cubic(rs, t);
  const a = cubic(ts, t);
  const dp = polarToCartesian(r, a);
  return dp;
};

const getBezierSpiralTan = (
  rs: [number, number, number, number],
  ts: [number, number, number, number],
  t: number
): Vec2 => {
  const r = cubic(rs, t);
  const dr = dcubic(rs, t);
  const a = cubic(ts, t);
  const da = dcubic(ts, t);
  const cosa = Math.cos(a);
  const sina = Math.sin(a);
  const dx = dr * cosa - r * da * sina;
  const dy = dr * sina + r * da * cosa;
  if(mag2([dx, dy]) < 1e-10) {
    return getSpiralTan(rs[0], rs[3], ts[0], ts[3], t);
  }
  return [dx, dy] as Vec2;
};

const getArcPoint = (
  pcen: Vec2,
  r: number,
  t0: number,
  t1: number,
  s: number
): Vec2 => {
  const dt = t1 - t0;
  const parX = r * Math.cos(t0 + s * dt);
  const parY = r * Math.sin(t0 + s * dt);
  return add(pcen, [parX, parY]);
};

const getArcTan = (r: number, t0: number, t1: number, s: number): Vec2 => {
  const dt = t1 - t0;
  const tx = -r * Math.sin(t0 + s * dt);
  const ty = r * Math.cos(t0 + s * dt);
  return [tx, ty];
};

const getArcTanFromPoints = (
  pcen: Vec2,
  p1: Vec2,
  p2: Vec2,
  s: number
): Vec2 => {
  const [r0, , angle0, dAngle] = getArcAngles(pcen, p1, p2);
  return getArcTan(r0, angle0, angle0 + dAngle, s);
};

const getSpiralPoints = (
  pcen: Vec2,
  r0: number,
  r1: number,
  t0: number,
  t1: number,
  s: number[] = []
): Vec2[] => {
  const dr = r1 - r0;
  return s.map((si) => getArcPoint(pcen, r0 + si * dr, t0, t1, si));
};

const getArcPoints = (
  pcen: Vec2,
  r0: number,
  t0: number,
  t1: number,
  t: number[] = []
): Vec2[] => {
  return getSpiralPoints(pcen, r0, r0, t0, t1, t);
};

const getBezierSpiralPoints = (
  pcen: Vec2,
  rs: [number, number, number, number],
  ts: [number, number, number, number],  s: number[] = []
): Vec2[] => {
  return s.map((si) => add(pcen, getBezierSpiralPoint(rs, ts, si)));
};


const getArcPointsFromPoints = (
  pcen: Vec2,
  p1: Vec2,
  p2: Vec2,
  t: number[] = []
): Vec2[] => {
  const [r0, , angle0, dAngle] = getArcAngles(pcen, p1, p2);
  return getSpiralPoints(pcen, r0, r0, angle0, angle0 + dAngle, t);
};

const threePointArc = (p0: Vec2, p1: Vec2, p2: Vec2) => {
  //arc from points p0 -> p1 -> p2
  const x1 = p0[0];
  const y1 = p0[1];
  const x2 = p1[0];
  const y2 = p1[1];
  const x3 = p2[0];
  const y3 = p2[1];
  const xy1sq = x1 ** 2 + y1 ** 2;
  const xy2sq = x2 ** 2 + y2 ** 2;
  const xy3sq = x3 ** 2 + y3 ** 2;

  const A = x1 * (y2 - y3) - y1 * (x2 - x3) + x2 * y3 - x3 * y2;
  const B = xy1sq * (y3 - y2) + xy2sq * (y1 - y3) + xy3sq * (y2 - y1);
  const C = xy1sq * (x2 - x3) + xy2sq * (x3 - x1) + xy3sq * (x1 - x2);
  const D =
    xy1sq * (x3 * y2 - x2 * y3) +
    xy2sq * (x1 * y3 - x3 * y1) +
    xy2sq * (x2 * y1 - x1 * y2);
  const xc = -B / 2.0 / A;  
  const yc = -C / 2.0 / A;
  //const R = Math.sqrt((B * B + C * C - 4.0 * A * D) / 4.0 / A);
  const cen = [xc, yc] as Vec2;
  const r = dist(cen, p0);
  const t0 = calcAngle(cen, p0);
  const dp0 = minus(p0, cen);
  const dp1 = minus(p1, cen);
  const dp2 = minus(p2, cen);
  const l0 = dist(p0, p1);
  const l1 = dist(p1,cen);
  const dtheta = signedAngle(dp0, dp1) + signedAngle(dp1, dp2);
  console.log(l0, r, dtheta);
  const dtheta2 = l1 < r ? -dtheta : dtheta;
  return [cen, r, t0, dtheta] as [Vec2, number, number, number];
};

const tanPointPointArc = (tan: Vec2, p0: Vec2, p1: Vec2) => {
  //       /|
  //      / |    sin(o) = l/r
  //     /  |    r = l*sin(theta)
  //  r /   | l
  //   /    |
  //  /     |
  // / o    |
  //---------
  //    d
  
  const tanN = normalize(tan);
  const l = dist(p0, p1);
  const biTan = [tanN[1], -tanN[0]] as Vec2;
  const dp = minus(p1, p0);
  const dotDpTan = dot(dp, tanN);
  const da = signedAngle(dp, tanN);

  let r = Number.isNaN(da) ? 0.0 : Math.abs(l / 2.0 / Math.sin(da));
  r = Math.max(Math.abs(r), 0.01);
  const cen = add(p0, sMult(Math.sign(da) * r, biTan));

  const t0 = calcAngle(cen, p0);
  const dp0 = minus(p0, cen);
  const dp1 = minus(p1, cen);
  const dtheta = signedAngle(dp0, dp1);
  const dtheta2 = dotDpTan > 0 ? dtheta : -Math.sign(da) * 2.0 * Math.PI + dtheta;
  return [cen, r, t0, dtheta2 ] as [Vec2, number, number, number];
};

const polarToCartesian = (r: number, theta: number): [number, number] => {
  return [r * Math.cos(theta), r * Math.sin(theta)];
};

const lerpAngle = (a0i: number, a1i: number, t: number): number => {
  const a0 = a0i < 0 ? a0i + 2 * Math.PI : a0i;
  const a1 = a1i < 0 ? a1i + 2 * Math.PI : a1i;
  let delta = (a1 - a0) % (2 * Math.PI);
  if (delta < -Math.PI) delta += 2 * Math.PI;
  if (delta > Math.PI) delta -= 2 * Math.PI;
  return a0 + t * delta;
};

const calcDrDtheta = (pivot: Vec2, p0: Vec2, p1: Vec2): [number, number] => {
  const dp0 = minus(p0, pivot);
  const dp1 = minus(p1, pivot);
  const r0 = mag(dp0);
  const r1 = mag(dp1);
  const dt = signedAngle(dp0, dp1);
  return [r1 - r0, dt];
};

// code from geeks for geeks
const isPointInPolygon = (point: Vec2, polygon: [number, number][]) => {
  const num_vertices = polygon.length;
  const x = point[0];
  const y = point[1];
  let inside = false;

  let p1 = polygon[0];
  let p2: Vec2 = [0, 0] as Vec2;

  for (let i = 1; i <= num_vertices; i++) {
    p2 = polygon[i % num_vertices];

    if (y > Math.min(p1[1], p2[1])) {
      if (y <= Math.max(p1[1], p2[1])) {
        if (x <= Math.max(p1[0], p2[0])) {
          const x_intersection =
            ((y - p1[1]) * (p2[0] - p1[0])) / (p2[1] - p1[1]) + p1[0];

          if (p1[0] === p2[0] || x <= x_intersection) {
            inside = !inside;
          }
        }
      }
    }

    p1 = p2;
  }

  return inside;
};

