import { useState, type ReactNode } from 'react';

interface Section {
  id: string;
  label: string;
  component: ReactNode;
}

const useSections = (sections: Section[], defaultSectionId: string) => {
  const [activeSection, setActiveSection] = useState<string>(defaultSectionId || sections[0]?.id || '');

  const buttons = sections.map((section) => (
    <button 
      key={section.id} 
      type="button"
      onClick={() => setActiveSection(section.id)}
      className={`text-sm mx-1 text-red-600 hover:underline focus:outline-none transition-colors ${
        activeSection === section.id ? 'font-bold' : ''
      }`}
    >
      {section.label}
    </button>
  ));

  // Render all components but control visibility
  const sectionsContent = (
    <div>
      {sections.map((section) => (
        <div 
          key={section.id}
          style={{ display: activeSection === section.id ? 'block' : 'none' }}
        >
          {section.component}
        </div>
      ))}
    </div>
  );

  return { buttons, sectionsContent };
};

export default useSections;