/*
 * Simple Markdown Renderer Component
 * 
 * FEATURES:
 * - Parse basic markdown syntax without external dependencies
 * - Headers (# ## ###), bold (**text**), italic (*text*)
 * - Links [text](url), lists (- item), code (`code`)
 * - Follow project's red-on-black color scheme
 * - Lightweight and self-contained
 * 
 * USAGE:
 * <MarkdownRenderer>
 *   # Heading
 *   ## Subheading
 *   **Bold text** and *italic text*
 * </MarkdownRenderer>
 */

import React, { ReactNode } from 'react';

interface MarkdownRendererProps {
  children: string;
  className?: string;
}

const MarkdownRenderer: React.FC<MarkdownRendererProps> = ({ 
  children, 
  className = '' 
}) => {
  // Simple markdown parser - no external dependencies
  const parseMarkdown = (text: string): ReactNode[] => {
    const lines = text.trim().split('\n');
    const elements: ReactNode[] = [];
    
    lines.forEach((line, index) => {
      const trimmedLine = line.trim();
      
      if (!trimmedLine) {
        elements.push(<br key={`br-${index}`} />);
        return;
      }
      
      // Headers
      if (trimmedLine.startsWith('### ')) {
        elements.push(
          <h3 key={index} className="text-lg font-bold text-red-500 mb-2">
            {parseInlineElements(trimmedLine.slice(4))}
          </h3>
        );
      } else if (trimmedLine.startsWith('## ')) {
        elements.push(
          <h2 key={index} className="text-xl font-bold text-red-500 mb-3">
            {parseInlineElements(trimmedLine.slice(3))}
          </h2>
        );
      } else if (trimmedLine.startsWith('# ')) {
        elements.push(
          <h1 key={index} className="text-2xl font-bold text-red-500 mb-4">
            {parseInlineElements(trimmedLine.slice(2))}
          </h1>
        );
      }
      // Lists
      else if (trimmedLine.startsWith('- ') || trimmedLine.startsWith('* ')) {
        elements.push(
          <li key={index} className="text-red-500 mb-1 ml-4">
            {parseInlineElements(trimmedLine.slice(2))}
          </li>
        );
      }
      // Regular paragraphs
      else {
        elements.push(
          <p key={index} className="text-red-500 mb-3 leading-relaxed">
            {parseInlineElements(trimmedLine)}
          </p>
        );
      }
    });
    
    return elements;
  };
  
  // Parse inline elements like bold, italic, links, code
  const parseInlineElements = (text: string): ReactNode[] => {
    const elements: ReactNode[] = [];
    let currentText = text;
    let keyCounter = 0;
    
    while (currentText.length > 0) {
      // Bold **text**
      const boldMatch = currentText.match(/\*\*(.*?)\*\*/);
      if (boldMatch && boldMatch.index !== undefined) {
        // Add text before bold
        if (boldMatch.index > 0) {
          elements.push(currentText.slice(0, boldMatch.index));
        }
        // Add bold element
        elements.push(
          <strong key={`bold-${keyCounter++}`} className="font-bold">
            {boldMatch[1]}
          </strong>
        );
        currentText = currentText.slice(boldMatch.index + boldMatch[0].length);
        continue;
      }
      
      // Italic *text*
      const italicMatch = currentText.match(/\*(.*?)\*/);
      if (italicMatch && italicMatch.index !== undefined) {
        if (italicMatch.index > 0) {
          elements.push(currentText.slice(0, italicMatch.index));
        }
        elements.push(
          <em key={`italic-${keyCounter++}`} className="italic">
            {italicMatch[1]}
          </em>
        );
        currentText = currentText.slice(italicMatch.index + italicMatch[0].length);
        continue;
      }
      
      // Code `text`
      const codeMatch = currentText.match(/`(.*?)`/);
      if (codeMatch && codeMatch.index !== undefined) {
        if (codeMatch.index > 0) {
          elements.push(currentText.slice(0, codeMatch.index));
        }
        elements.push(
          <code key={`code-${keyCounter++}`} className="bg-red-900/20 text-red-400 px-1 py-0.5 rounded font-mono text-sm">
            {codeMatch[1]}
          </code>
        );
        currentText = currentText.slice(codeMatch.index + codeMatch[0].length);
        continue;
      }
      
      // Links [text](url)
      const linkMatch = currentText.match(/\[([^\]]+)\]\(([^)]+)\)/);
      if (linkMatch && linkMatch.index !== undefined) {
        if (linkMatch.index > 0) {
          elements.push(currentText.slice(0, linkMatch.index));
        }
        elements.push(
          <a 
            key={`link-${keyCounter++}`}
            href={linkMatch[2]} 
            className="text-red-500 underline hover:text-red-400 transition-colors duration-200"
            target="_blank"
            rel="noopener noreferrer"
          >
            {linkMatch[1]}
          </a>
        );
        currentText = currentText.slice(linkMatch.index + linkMatch[0].length);
        continue;
      }
      
      // No more special elements, add remaining text
      elements.push(currentText);
      break;
    }
    
    return elements;
  };

  return (
    <div className={`text-red-500 ${className}`}>
      {parseMarkdown(children)}
    </div>
  );
};

export default MarkdownRenderer;