/*
 * Requirements for the BezierSpiral Component:
 *
 * TOOLBAR SETUP:
 * - Implement a toolbar featuring two buttons: one for the pencil tool and another for the edit tool.
 * - Use a style that mirrors the social icons component.
 * - Position the toolbar so it hovers over the drawing area.
 * - Arrange the toolbar using a vertical flex layout.
 * - Group the buttons together in a div container.
 *
 * BUTTON FUNCTIONALITY:
 * - Pencil tool button: When activated, sets toolEnabled to true.
 * - Edit tool button: When activated, sets toolEnabled to false.
 * - Incorporate Lumix icons for both buttons.
 * - Ensure the pencil and edit tool buttons toggle as a group.
 *
 * DESIGN VISUALS:
 * - Adhere to the styling guidelines from the SocialIcon component.
 * - Place the toolbar so it overlays the drawing surface.
 * - Keep hover effects and visual feedback intact.
 */

import React, { useCallback, useState } from 'react';
import { Pencil, Edit3 } from 'lucide-react';
import type { Vec10 } from './useBezierSpiral';
import { useBezierSpiralData, useBezierSpiralTool } from './useBezierSpiral';


type Bezier = {type: "bezier", data: Array<number>}
type Spiral = {type: "spiral", data: Array<number>}

export const BezierSpiral: React.FC = () => {
  // state: an array of [x, y] tuples
  const [data, setData] = useState<Vec10[]>([]);
  const [toolEnabled, setToolEnabled]= useState<boolean>(false);
  const addData = useCallback((newData: Vec10) => {
    setData((prevData: Vec10[]) => [...prevData, newData]);
  }, []);

  const svgRef = React.useRef<SVGSVGElement>(null);
  const {drawToolMarks} = useBezierSpiralTool( data, addData, svgRef, toolEnabled);
  const {renderData, handleMouseMove, handleMouseUp, resetEditing} = useBezierSpiralData(data, setData, svgRef);

  return (
    <div style={{ position: 'relative' }}>
      {/* Toolbar - positioned to hover over the drawing surface */}
      <div style={{
        position: 'absolute',
        top: '10px',
        left: '10px',
        zIndex: 10,
        display: 'flex',
        flexDirection: 'column',
        gap: '4px'
      }}>
        {/* Pencil Tool Button */}
        <button
          title="Pencil Tool"
          type="button"
          onClick={() => setToolEnabled(true)}
          style={{
            width: '40px',
            height: '40px',
            border: 'none',
            borderRadius: '8px',
            backgroundColor: toolEnabled ? '#ff0000' : 'transparent',
            color: toolEnabled ? 'white' : '#ff0000',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s ease',
            filter: toolEnabled ? 'none' : 'hue-rotate(60deg)'
          }}
          onMouseEnter={(e) => {
            if (!toolEnabled) {
              e.currentTarget.style.backgroundColor = 'rgba(255, 0, 0, 0.1)';
            }
          }}
          onMouseLeave={(e) => {
            if (!toolEnabled) {
              e.currentTarget.style.backgroundColor = 'transparent';
            }
          }}
        >
          <Pencil size={20} />
        </button>

        {/* Edit Tool Button */}
        <button
          title="Edit Tool"
          type="button"
          onClick={() => setToolEnabled(false)}
          style={{
            width: '40px',
            height: '40px',
            border: 'none',
            borderRadius: '8px',
            backgroundColor: !toolEnabled ? '#ff0000' : 'transparent',
            color: !toolEnabled ? 'white' : '#ff0000',
            cursor: 'pointer',
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            transition: 'all 0.2s ease',
            filter: !toolEnabled ? 'none' : 'hue-rotate(60deg)'
          }}
          onMouseEnter={(e) => {
            if (toolEnabled) {
              e.currentTarget.style.backgroundColor = 'rgba(255, 0, 0, 0.1)';
            }
          }}
          onMouseLeave={(e) => {
            if (toolEnabled) {
              e.currentTarget.style.backgroundColor = 'transparent';
            }
          }}
        >
          <Edit3 size={20} />
        </button>
      </div>

      <svg
        ref={svgRef}
        width="500"
        height="300"
        style={{ border: '1px solid gray' }}
        onMouseDown={resetEditing}
        onMouseMove={handleMouseMove} 
        onMouseUp={handleMouseUp}
        onMouseLeave={handleMouseUp}
      >
        <title>Bezier Spiral SVG</title>
        {drawToolMarks()}
        {renderData()}
      </svg>
    </div>
  );
};

export default BezierSpiral;