/*
 * Simple LaTeX Renderer Component
 * 
 * FEATURES:
 * - Render LaTeX mathematical expressions using KaTeX
 * - Simple API: just wrap LaTeX code in the component
 * - Follow project's red-on-black color scheme
 * - Error handling for malformed expressions
 * 
 * USAGE:
 * <LaTeXRenderer>
 *   x = \frac{-b \pm \sqrt{b^2 - 4ac}}{2a}
 * </LaTeXRenderer>
 */

import React from 'react';
import katex from 'katex';
import 'katex/dist/katex.min.css';

interface LaTeXRendererProps {
  children: string;
  className?: string;
  displayMode?: boolean; // true for block equations, false for inline
}

const LaTeXRenderer: React.FC<LaTeXRendererProps> = ({ 
  children, 
  className = '',
  displayMode = true 
}) => {
  try {
    const html = katex.renderToString(children.trim(), {
      displayMode,
      throwOnError: false,
      errorColor: '#ff4444',
      colorIsTextColor: true,
    });

    return (
      <div 
        className={`text-red-500 ${displayMode ? 'my-4 text-center' : 'inline'} ${className}`}
        dangerouslySetInnerHTML={{ __html: html }}
      />
    );
  } catch (error) {
    return (
      <div className={`text-red-400 bg-red-900/20 p-2 rounded ${className}`}>
        <span className="font-mono text-sm">LaTeX Error: {children}</span>
      </div>
    );
  }
};

export default LaTeXRenderer;