import Lin2D, { type Vec2, type Mat2 } from "./Lin2D";
import { useCallback, useEffect, useState } from "react";
import React from "react";

type Vec4 = [number, number, number, number];

export type Vec10 = [
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number
];

const TS = 4.0; // Tangent Scale
const iTS = 1.0 / TS;
const getBezierSpiralControlPoints = (data: Vec10): Vec2[] => {
  const cen = data.slice(0, 2) as Vec2;
  const rs = data.slice(2, 6) as Vec4;
  const ts = data.slice(6, 10) as Vec4;
  const points = rs.map((e: number, i: number) =>
    Lin2D.add(cen, Lin2D.polarToCartesian(e, ts[i]))
  );
  const tan0 = Lin2D.sMult(iTS, Lin2D.getBezierSpiralTan(rs, ts, 0.0));
  const tan1 = Lin2D.sMult(iTS, Lin2D.getBezierSpiralTan(rs, ts, 1.0));
  const tp0 = Lin2D.add(points[0], tan0);
  const tp1 = Lin2D.minus(points[3], tan1);
  return [cen, points[0], points[3], tp0, tp1];
};

const getBezierSpiralDatum = (
  pcen: Vec2,
  t0: number,
  t3: number,
  r0: number,
  r3: number,
  tan0: Vec2,
  tan1: Vec2
) => {
  let r1 = r0;
  let r2 = r3;
  let t1 = t0;
  let t2 = t3;
  if (Lin2D.dist(tan0, [0, 0]) > 1e-6) {
    [r1, t1] = Lin2D.calcBezierControlFromTangent(
      [r0, t0],
      Lin2D.sMult(TS, tan0)
    );
  }
  if (Lin2D.dist(tan1, [0, 0]) > 1e-6) {
    [r2, t2] = Lin2D.calcBezierControlFromTangent(
      [r3, t3],
      Lin2D.sMult(TS, tan1)
    );
  }
  return [...pcen, r0, r1, r2, r3, t0, t1, t2, t3] as Vec10;
};

const moveBezierSpiral = (datum: Vec10, pointId: number, newPnt: Vec2) => {
  const newShape = { ...datum } as Vec10;
  const cen = datum.slice(0, 2) as Vec2;

  const rs = datum.slice(2, 6) as Vec4;
  const ts = datum.slice(6, 10) as Vec4;
  const ps = rs.map((e: number, i: number) =>
    Lin2D.add(cen, Lin2D.polarToCartesian(e, ts[i]))
  );
  const dps = rs.map((e: number, i: number) =>
    Lin2D.polarToCartesian(e, ts[i])
  );

  if (pointId === 3) {
    const tan = Lin2D.sMult(TS, Lin2D.minus(newPnt, ps[0]));
    const [r, o] = Lin2D.calcBezierControlFromTangent([rs[0], ts[0]], tan);
    const nrs = [...rs];
    const nts = [...ts];
    nrs[1] = r;
    nts[1] = o;
    return [...cen, ...nrs, ...nts] as Vec10;
  }
  if (pointId === 4) {
    const tan = Lin2D.sMult(TS, Lin2D.minus(newPnt, ps[3]));
    const [r, o] = Lin2D.calcBezierControlFromTangent([rs[3], ts[3]], tan);
    const nrs = [...rs];
    const nts = [...ts];
    nrs[2] = r;
    nts[2] = o;

    return [...cen, ...nrs, ...nts] as Vec10;
  }

  let ncen = cen;
  const nps = [...ps];
  if (pointId === 0) {
    ncen = newPnt;
  } else if (pointId === 1) {
    nps[0] = newPnt;
  } else if (pointId === 2) {
    nps[3] = newPnt;
  }

  //if (pointId !== 1 && pointId !== 2) {
    //maintain tangents
    const tan0 = Lin2D.getBezierSpiralTan(rs, ts, 0.0);
    const tan1 = Lin2D.getBezierSpiralTan(rs, ts, 1.0);

    const ndps = nps.map((p) => Lin2D.minus(p, ncen));
    const dts = ndps.map((p, i) => Lin2D.signedAngle(p, dps[i]));
    if (dts.some((p) => Number.isNaN(p))) return datum;
    const nrs = ndps.map((p) => Lin2D.mag(p));
    const nts = ts.map((t, i) => t - dts[i]);
    const [r1, o1] = Lin2D.calcBezierControlFromTangent([nrs[0], nts[0]], tan0);
    const [r2, o2] = Lin2D.calcBezierControlFromTangent(
      [nrs[3], nts[3]],
      Lin2D.sMult(-1.0, tan1)
    );
    nrs[1] = r1;
    nrs[2] = r2;
    nts[1] = o1;
    nts[2] = o2;
    return [...ncen, ...nrs, ...nts] as Vec10;
  //}

};

export interface BezierSpiralProps extends React.SVGProps<SVGPolylineElement> {
  datum: Vec10;
}

export const BezierSpiral: React.FC<BezierSpiralProps> = ({
  datum,
  points, // remove any incoming points to avoid duplicate assignment
  ...props
}) => {
  const N = 128;
  const s = Array.from({ length: N }, (_, i) => i / (N - 1));
  const pcen = datum.slice(0, 2) as Vec2;
  const rs = datum.slice(2, 6) as Vec4;
  const ts = datum.slice(6, 10) as Vec4;
  const pts = Lin2D.getBezierSpiralPoints(pcen, rs, ts, s);
  const polyPoints = pts.map((pt) => pt.join(",")).join(" ");

  return <polyline points={polyPoints} {...props} />;
};

export interface PointProps extends React.SVGProps<SVGCircleElement> {
  datum: Vec2;
  r?: number;
  fill?: string;
}
export const Point: React.FC<PointProps> = ({
  datum,
  r = 3,
  fill = "red",
  ...props
}) => {
  return (
    <circle
      cx={datum[0]}
      cy={datum[1]}
      r={r}
      fill={fill}
      stroke="none"
      {...props}
    />
  );
};

export interface TangentProps extends React.SVGProps<SVGLineElement> {
  datum: [Vec2, Vec2];
  stroke?: string;
  strokeWidth?: number;
}
export const Tangent: React.FC<TangentProps> = ({
  datum,
  stroke = "gray",
  strokeWidth = 1,
  ...props
}) => {
  const [p0, p1] = datum;
  return (
    <line
      x1={p0[0]}
      y1={p0[1]}
      x2={p1[0]}
      y2={p1[1]}
      stroke={stroke}
      strokeWidth={strokeWidth}
      {...props}
    />
  );
};

export const useBezierSpiralTool = (
  data: Vec10[],
  addData: (val: Vec10) => void,
  parentDivRef: React.RefObject<SVGSVGElement>,
  toolEnabled: boolean
) => {
  const [dTheta, setDTheta] = useState(0);
  const [points, setPoints] = useState<Vec2[]>([]);
  const [cpoint, setCPoint] = useState<Vec2>([0, 0]);
  const mousedown = useCallback(
    (event: MouseEvent) => {
      const curPoint: Vec2 = [event.offsetX, event.offsetY];
      points.push(curPoint);

      event.preventDefault();
      if (points.length > 4) {
        const [p0, p1, cen, p3, p2] = points;

        const tan0 = Lin2D.minus(p1, p0);
        const tan1 = Lin2D.minus(p2, p3);
        const t0 = Lin2D.calcAngle(cen, p0);
        const t3 = t0 + dTheta;

        const r3 = Lin2D.dist(p3, cen);
        const r0 = Lin2D.dist(p0, cen);
        const datum = getBezierSpiralDatum(cen, t0, t3, r0, r3, tan0, tan1);
        addData(datum);
        const rs = datum.slice(2, 6) as Vec4;
        const ts = datum.slice(6, 10) as Vec4;
        const np1 = Lin2D.add(cen, Lin2D.getBezierSpiralPoint(rs, ts, 1.0));
        const ntan1 = Lin2D.sMult(iTS, Lin2D.getBezierSpiralTan(rs, ts, 1.0));
        const newPoints = [np1, Lin2D.add(np1, ntan1)];
        setPoints(newPoints);
        setDTheta(0);
      } else {
        setPoints([...points]);
      }
      return null;
    },
    [data, points, dTheta]
  );

  const mousemove = useCallback(
    (event: MouseEvent) => {
      const currentPoint: Vec2 = [event.offsetX, event.offsetY];

      if (points.length === 3) {
        const [p0, p1, cen] = [points[0], points[1], points[2]];
        const t0 = Lin2D.calcAngle(cen, p0);
        const dpc = Lin2D.minus(p0, cen);
        const dp0 = Lin2D.minus(cpoint, cen);
        const dp1 = Lin2D.minus(currentPoint, cen);
        if (Math.abs(dTheta) < 1e-2) {
          const stheta = Lin2D.signedAngle(dpc, dp1);
          setDTheta(dTheta + stheta);
        } else {
          const stheta = Lin2D.signedAngle(dp0, dp1);
          setDTheta(dTheta + stheta);
        }
      }
      setCPoint(currentPoint);
    },
    [data, points, dTheta, cpoint]
  );

  const drawToolMarks = useCallback(() => {
    if (points.length === 1) {
      return (
        <>
          <Point datum={points[0]} />
          <Tangent datum={[points[0], cpoint]} />
        </>
      );
    }
    if (points.length === 2) {
      return (
        <>
          <Point datum={points[0]} />
          <Point datum={cpoint} />
          <Tangent datum={[points[0], points[1]]} />
          <Tangent datum={[cpoint, points[0]]} />
        </>
      );
    }
    if (points.length >= 3) {
      const [p0, p1, cen] = [points[0], points[1], points[2]];
      const p3 = points.length === 4 ? points[3] : cpoint;
      const p2 = cpoint;

      const tan0 = Lin2D.minus(p1, p0);
      const tan1 = Lin2D.minus(p2, p3);
      const r0 = Lin2D.dist(p0, cen);
      const t0 = Lin2D.calcAngle(cen, p0);
      const r1 = Lin2D.dist(p3, cen);
      const t1 = t0 + dTheta;
      const datum = getBezierSpiralDatum(cen, t0, t1, r0, r1, tan0, tan1);
      return (
        <>
          <Point datum={p0} />
          <Point datum={cen} />
          <Tangent datum={[p0, cen]} />
          <Tangent datum={[p0, p1]} />
          <Tangent datum={[p2, p3]} />
          <BezierSpiral
            datum={datum}
            stroke="gray"
            strokeWidth="1"
            fill="none"
          />
        </>
      );
    }

    return null;
  }, [points, cpoint, dTheta]);

  useEffect(() => {
    if (!parentDivRef.current) return;
    const handleMouseDown = (event: MouseEvent) => {
      mousedown(event);
    };
    const handleMouseMove = (event: MouseEvent) => {
      mousemove(event);
    };

    const parent = parentDivRef.current;
    if (toolEnabled) {
      parent.addEventListener("mousedown", handleMouseDown);
      parent.addEventListener("mousemove", handleMouseMove);
    } else {
      if (points.length > 0) setPoints([]);
      parent.removeEventListener("mousedown", handleMouseDown);
      parent.removeEventListener("mousemove", handleMouseMove);
    }
    return () => {
      if (toolEnabled) {
        parent.removeEventListener("mousedown", handleMouseDown);
        parent.removeEventListener("mousemove", handleMouseMove);
      }
    };
  }, [parentDivRef, mousedown, mousemove, toolEnabled]);
  // Return the controls accordingly (e.g., event handlers and tool marks)
  return {
    drawToolMarks,
    // up: () => {} (if needed)
  };
};

export const useBezierSpiralData = (
  data: Vec10[],
  setData: (val: Vec10[]) => void,
  parentDivRef: React.RefObject<SVGSVGElement>
) => {
  const [datumIndex, setDatumIndex] = useState<number | null>(null);
  const [draggingIndex, setDraggingIndex] = useState<number | null>(null);
  const [controlPoints, setControlPoints] = useState<Vec2[]>([]);

  const resetEditing = () => {
    setDatumIndex(null);
    setDraggingIndex(null);
    setControlPoints([]);
  };

  const handleSpiralMouseDown =
    (index: number) => (event: React.MouseEvent<SVGPolylineElement>) => {
      const datum = data[index];
      const ctrlPoints = getBezierSpiralControlPoints(datum);
      setControlPoints(ctrlPoints);
      setDatumIndex(index);
      setDraggingIndex(0); // Start dragging the first control point
      event.preventDefault();
      event.stopPropagation();
    };

  const handleCptMouseDown =
    (index: number) => (event: React.MouseEvent<SVGCircleElement>) => {
      setDraggingIndex(index);
      event.preventDefault();
      event.stopPropagation();
    };

  const handleMouseMove = useCallback(
    (event: React.MouseEvent<SVGSVGElement>) => {
      if (draggingIndex === null) return;

      // Get SVG position
      const svg = event.currentTarget;
      const pt = svg.createSVGPoint();
      pt.x = event.clientX;
      pt.y = event.clientY;
      const svgP = pt.matrixTransform(svg.getScreenCTM()?.inverse());
      const tan0 = Lin2D.minus(controlPoints[3], controlPoints[1]);
      const tan1 = Lin2D.minus(controlPoints[4], controlPoints[2]);

      const newPoints = controlPoints.map((point, index) =>
        index === draggingIndex ? [svgP.x, svgP.y] : point
      ) as Vec2[];

      if (draggingIndex === 1 || draggingIndex === 2) {
        newPoints[3] = Lin2D.add(newPoints[1], tan0);
        newPoints[4] = Lin2D.add(newPoints[2], tan1);
      }
      setControlPoints(newPoints);
    },
    [draggingIndex, controlPoints, datumIndex, data, setControlPoints]
  );

  const handleMouseUp = () => {
    setDraggingIndex(null);
  };

  useEffect(() => {
    if (datumIndex === null) return;
    if (draggingIndex === null) return;
    const oldDatum = data[datumIndex];
    const newDatum = moveBezierSpiral(
      data[datumIndex],
      draggingIndex,
      controlPoints[draggingIndex]
    );
    console.log(oldDatum, newDatum);

    const newData = [...data];
    newData[datumIndex] = newDatum;

    //now fix adjacent control points
    if ((draggingIndex === 1 || draggingIndex === 3) && datumIndex > 0) {
      const [p0, p1] = [controlPoints[1], controlPoints[3]];
      const tan = Lin2D.minus(p1, p0);
      const np3 = p0;
      const np2 = Lin2D.minus(p0, tan);
      newData[datumIndex - 1] = moveBezierSpiral(
        newData[datumIndex - 1],
        2,
        np3
      );
      newData[datumIndex - 1] = moveBezierSpiral(
        newData[datumIndex - 1],
        4,
        np2
      );
    }

    if (
      (draggingIndex === 2 || draggingIndex === 4) &&
      datumIndex < data.length - 1
    ) {
      const [p0, p1] = [controlPoints[2], controlPoints[4]];
      const tan = Lin2D.minus(p1, p0);
      const np0 = p0;
      const np1 = Lin2D.minus(p0, tan);
      newData[datumIndex + 1] = moveBezierSpiral(
        newData[datumIndex + 1],
        1,
        np0
      );
      newData[datumIndex + 1] = moveBezierSpiral(
        newData[datumIndex + 1],
        3,
        np1
      );
    }

    setData(newData);
  }, [controlPoints, draggingIndex, datumIndex]);

  const renderData = useCallback(
    () => (
      <>
        {data.map((datum, index) => (
          <BezierSpiral
            key={`bezier-spiral-${datum}`}
            datum={datum}
            stroke={datumIndex === index ? "blue" : "gray"}
            strokeWidth={3.0}
            onMouseDown={handleSpiralMouseDown(index)}
            fill="none"
          />
        ))}
        {controlPoints.length > 0 && (
          <>
            <Tangent datum={[controlPoints[1], controlPoints[3]]}/>
            <Tangent datum={[controlPoints[2], controlPoints[4]]}/>
            {controlPoints.map(([x, y], index) => (
              <Point
                key={`${x}-${y}`}
                datum={[x, y]}
                r={4}
                fill="red"
                onMouseDown={handleCptMouseDown(index)}
              />
            ))}
          </>
        )}
      </>
    ),

    [data, controlPoints]
  );

  return { renderData, handleMouseMove, handleMouseUp, resetEditing };
};
